import React, { Component } from "react";
import { fetchTopArtistsInfo } from "../../redux/store";
import { fetchAlbums } from "../../redux/store";
import { fetchTopTracks } from "../../redux/store";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import ArtistHeader from "./artistheader";
import ArtistTags from "./artisttags";
import ArtistBio from "./artistbio";
import AlbumsAndSimilarArtistsSection from "./AlbumsAndSimilarArtists/section";
import TopTracks from "./TopTracks/tracksection";

class ArtistDetails extends Component {
  state = {
    loading: true,
    artistName: this.props.match.params.name
  };
  componentDidMount() {
    if (this.props.user.userInfo !== null) {
      Promise.all([
        fetchTopArtistsInfo(this.state.artistName),
        fetchAlbums(this.state.artistName),
        fetchTopTracks(this.state.artistName)
      ]).then(() => {
        this.setState({
          loading: false
        });
      });
    } else {
      this.props.history.replace({ pathname: "/login" });
    }
  }

  // UNSAFE_componentWillReceiveProps(nextProps) {
  //   if (nextProps.match.params.name !== this.props.match.params.name) {
  //     this.setState({
  //       loading: true
  //     });
  //     Promise.all([
  //       fetchTopArtistsInfo(nextProps.match.params.name),
  //       fetchAlbums(nextProps.match.params.name),
  //       fetchTopTracks(nextProps.match.params.name)
  //     ]).then(() =>
  //       this.setState({
  //         loading: false,
  //         artistName: nextProps.match.params.name
  //       })
  //     );
  //   }
  // }

  componentDidUpdate(prevProps) {
    if (prevProps.match.params.name !== this.props.match.params.name) {
      this.setState({
        loading: true
      });
      Promise.all([
        fetchTopArtistsInfo(this.props.match.params.name),
        fetchAlbums(this.props.match.params.name),
        fetchTopTracks(this.props.match.params.name)
      ]).then(() =>
        this.setState({
          loading: false,
        })
      );
    }

  }

  render() {
    const artist = this.props.match.params.name;
    const hasArtistInfo = this.props.artistInfo[artist];
    if (this.state.loading === false) {
      return (
        <div
          className="container"
          style={{ marginTop: "10px", marginBottom: "20px" }}
        >
          <ArtistHeader
            img={hasArtistInfo && this.props.artistInfo[artist].image[2]["#text"]}
            name={hasArtistInfo && this.props.artistInfo[artist].name}
          />
          <div className="artist-details-container">
            <ArtistTags tags={(hasArtistInfo && this.props.artistInfo[artist].tags.tag) || []} />
            <ArtistBio
              bio={hasArtistInfo && this.props.artistInfo[artist].bio.summary.split(" <a")[0]}
            />
            <AlbumsAndSimilarArtistsSection artistName={artist} id={"here"} />
            <TopTracks />
          </div>
        </div>
      );
    } else {
      return (
        <div className="loader-container">
          <Loader type="Audio" color="#black" height="250" width="250" />
        </div>
      );
    }
  }
}

const mapStateToProps = state => ({
  user: state.user || {},
  artistInfo: state.artistsInfo || {}
});

const ArtistDetailsContainer = connect(
  mapStateToProps,
  null
)(ArtistDetails);

export default ArtistDetailsContainer;
