import React from "react";
import { Link } from "react-router-dom";

const Item = props => {
  const style = {
    background: `repeating-linear-gradient(
        to bottom,
        rgba(0, 0, 0, 0.67),
        rgba(0, 0, 0, 0.67)
      ), 
      url(${props.image})`,
    backgroundSize: "cover"
  };
  return (
    <div className="col-lg-2 col-md-3 col-sm-4 col-xs-6" style={style}>
      <div className="search-result">
        <div className="search-artist-name">
          <Link
            style={{ color: "white", textDecoration: "none" }}
            to={`/artists/${props.name}`}
          >
            {props.name}
          </Link>
        </div>
        <div className="search-artist-listeners">
        {props.type === "album"
            ? props.artist 
            : null}
        </div>
      </div>
    </div>
  );
};

export default Item;
