import React from "react";
import Item from "./item";
import { connect } from "react-redux";
import { Link } from "react-router-dom";

const ItemList = props => {
  const tagTopArtists = props.topArtists[props.tagName];
  const tagTopAlbums = props.topAlbums;
  return (
    <div className="display-search-results">
      <div className="alb-section" id="alb-section">
        <div className="title">Top Artists</div>
        <div>
          {tagTopArtists
            .filter((item, i) => i < 5)
            .map((item, i) => (
              <Link
                key={i}
                to={`/artists/${item.name}`}
                style={{ color: "white", textDecoration: "none" }}
              >
                <Item
                  type={"artist"}
                  name={item.name}
                  image={item.image[2]["#text"]}
                />
              </Link>
            ))}
        </div>
      </div>

      <div className="similar-section">
        <div className="title">Top Albums</div>
        {tagTopAlbums.map((item, i) => (
          <Item
            type={"album"}
            artist={item.artist.name}
            name={item.name}
            key={i}
            image={item.image[2]["#text"]}
          />
        ))}
      </div>
    </div>
  );
};
const mapStateToProps = state => ({
  topArtists: state.tagTopArtists || {},
  topAlbums: state.tagAlbums.tagAlbums || []
});

const ListContainer = connect(
  mapStateToProps,
  null
)(ItemList);

export default ListContainer;
