import React, { Component } from "react";
import { Icon } from "antd";
import { secondsToMinutes } from "../../../helpers/workmates";
import ModalVideo from "react-modal-video";
import "/Users/klajdbudlla/Desktop/SoftUp/LastFmProject/last-fm-clone/node_modules/react-modal-video/css/modal-video.min.css";

class Track extends Component {
  state = {
    heartIcon: {
      theme: "outlined",
      color: "#666666",
      clicked: false
    },
    playIcon: {
      theme: "outlined",
      color: "#666666",
      clicked: false
    },
    isOpen: false
  };

  handleClickHeart() {
    if (this.state.heartIcon.clicked === false) {
      const favTracksArray = JSON.parse(localStorage.getItem("favorites"));
      favTracksArray.push(this.props.name);
      localStorage.setItem("favorites", JSON.stringify(favTracksArray));
      this.setState({
        heartIcon: {
          theme: "filled",
          color: "red",
          clicked: true
        }
      });
    } else {
      const favTracksArray = JSON.parse(localStorage.getItem("favorites"));
      const index = favTracksArray.indexOf(this.props.name);
      if (index > -1) {
        favTracksArray.splice(index, 1);
        localStorage.setItem("favTracks", JSON.stringify(favTracksArray));
        localStorage.setItem("favorites", JSON.stringify(favTracksArray));
      }
      this.setState({
        heartIcon: {
          theme: "outlined",
          color: "#666666",
          clicked: false
        }
      });
    }
  }

  openModal() {
    this.setState({ isOpen: true });
  }
  handleClickPlay() {
    if (this.state.playIcon.clicked === false) {
      this.setState({
        playIcon: {
          theme: "filled",
          color: "black",
          clicked: true
        }
      });
      this.openModal();
    } else {
      this.setState({
        playIcon: {
          theme: "outlined",
          color: "#666666",
          clicked: false
        }
      });
    }
  }

  componentDidMount() {
    if (JSON.parse(localStorage.getItem("favorites")) !== null) {
      const actualFavorites = JSON.parse(localStorage.getItem("favorites"));
      if (actualFavorites.indexOf(this.props.name) > -1) {
        this.setState({
          heartIcon: {
            theme: "filled",
            color: "red",
            clicked: true
          }
        });
      }
    }
  }

  render() {
    return (
      <tr>
        <th scope="row" />
        <td>
          <div className="track">
            <div className="favorites-icon">
              <Icon
                type="heart"
                className="heart"
                theme={this.state.heartIcon.theme}
                onClick={() => this.handleClickHeart()}
                style={{ color: this.state.heartIcon.color }}
              />
            </div>
            <div className="play-icon">
              <ModalVideo
               
                channel="youtube"
                isOpen={this.state.isOpen}
                videoId="h5RJsAa2iyY"
                onClose={() =>
                  this.setState({
                    isOpen: false,
                    playIcon: {
                      theme: "outlined",
                      color: "#666666",
                      clicked: false
                    }
                  })
                }
                 style={{ width: "250px" }}
              />
              <Icon
                type="play-circle"
                className="play"
                theme={this.state.playIcon.theme}
                onClick={() => this.handleClickPlay()}
                style={{ color: this.state.playIcon.color }}
              />
            </div>
            <div className="track-name">{this.props.name}</div>
            <div className="track-length">
              <div className="track-playcount"></div>
              <div> {secondsToMinutes(this.props.duration)}</div>
            </div>
          </div>
        </td>
      </tr>
    );
  }
}

export default Track;
