import React, { Component } from "react";
import { fetchTagTopArtists } from "../../redux/store";
import { fetchTopTagsBio } from "../../redux/store";
import { fetchTagAlbums } from "../../redux/store";
import { fetchTagTopTracks } from "../../redux/store";
import Loader from "react-loader-spinner";
import { connect } from "react-redux";
import TagHeader from "./tagsheader";
import TagBio from "./tagbio";
import AlbumsAndArtistsSection from "./AlbumsAndRelatedTags/section";
import TopTracks from "./TopTracks/tracksection";

class TagDetails extends Component {
  state = {
    loading: true
  };
  componentDidMount() {
    if (this.props.user.userInfo !== null) {
      Promise.all([
        fetchTagTopArtists(this.props.match.params.name),
        fetchTopTagsBio(this.props.match.params.name),
        fetchTagTopTracks(this.props.match.params.name),
        fetchTagAlbums(this.props.match.params.name)
      ]).then(() => {
        this.setState({
          loading: false
        });
      });
    } else {
      this.props.history.replace({ pathname: "/login" });
    }
  }
  render() {
    const artist = this.props.match.params.name;
    if (this.state.loading === false) {
      return (
        <div
          className="container"
          style={{ marginTop: "10px", marginBottom: "20px" }}
        >
          <TagHeader name={this.props.match.params.name} />
          <div className="artist-details-container">
            <TagBio
              bio={
                this.props.tags[this.props.match.params.name].split(" <a")[0]
              }
            />
            <AlbumsAndArtistsSection tagName={artist} id={"here"} />
            <TopTracks />
          </div>
        </div>
      );
    } else {
      return (
        <div className="loader-container">
          <Loader type="Audio" color="#black" height="250" width="250" />
        </div>
      );
    }
  }
}

const mapStateToProps = state => ({
  user: state.user || {},
  artistInfo: state.artistsInfo || {},
  tags: state.bio.tagsBio || {}
});

const TagDetailsContainer = connect(
  mapStateToProps,
  null
)(TagDetails);

export default TagDetailsContainer;
